import React,{useState, useEffect} from 'react'
import {Link,useParams} from "react-router-dom";
import fireDb from '../firebase';
import "./View.css";


const View = () => {

    const[user, setUser] = useState({});
    const{id} = useParams();

    useEffect(() =>{
        fireDb.child(`EmployeeDetials/${id}`).get().then((snapshot)=>{
            if(snapshot.exists()){
                setUser({...snapshot.val()});
              }else{
                  setUser({}); 
              }

        })
    },[id]);
        console.log("user",user);
    return (
        <div style={{marginTop:"150px"}}>
        <div className="card">
            <div className="card-header">
            <div className="h1"><p>Employee Details</p></div>
            </div>
            <div className="container">
            <br />
            <div>
            <div className="l1"><strong>ID</strong></div>
                <div className="l2"><strong>:</strong></div>
               <div className="i">
               <span>{id}</span>
               </div>
            </div>
            <br />
            <div>
            <div className="l1"><strong>Name</strong></div>
                <div className="l2"><strong>:</strong></div>
               <div className="i">
               <span>{user.name}</span>
               </div>
            </div>
            <br />
            <div>
            <div className="l1"><strong>EmployeeID</strong></div>
                <div className="l2"><strong>:</strong></div>
               <div className="i">
               <span>{user.empid}</span>
               </div>
            </div>
            <br />
            <div>
            <div className="l1"><strong>EmailID</strong></div>
                <div className="l2"><strong>:</strong></div>
               <div className="i">
               <span>{user.primaryemailid}</span>
               </div>
            </div>
            <br />
            <div>
            <div className="l1"><strong>EntityID</strong></div>
                <div className="l2"><strong>:</strong></div>
               <div className="i">
               <span>{user.entityid}</span>
               </div>
            </div>
            <br />
            <div>
            <div className="l1"><strong>CorporateName</strong></div>
                <div className="l2"><strong>:</strong></div>
               <div className="i">
               <span>{user.corporatename}</span>
               </div>
            </div>
            <div>
            <br />
            <div className="l1"><strong>Mobileno</strong></div>
                <div className="l2"><strong>:</strong></div>
               <div className="i">
               <span>{user.mobileno}</span>
               </div>
            </div>
                <div className="bt-div">
                <Link to="/">
                    <button className="btn btn-view bc">Close</button>
                </Link>
                </div>
                
            </div>
            
        </div>
        </div>
    );
};

export default View;
