import React,{useState,useEffect} from "react";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";
import fireDb from "../firebase";
import "./Home.css";
import "./AddEmployee";

const Home = () => {

    const [data,setData] =useState({});
// snapshot the data from db to UI
    useEffect(()=> {
        fireDb.child("EmployeeDetials").on("value", (snapshot) =>{
            if(snapshot.val()!==null){
                setData({...snapshot.val() });
            }else{
                setData({});

            }
        });
        return() => {
            setData({});
        };
    }, []);

    // delete btn function
    const onDelete = (id) =>{
        if(window.confirm("Are you sure want to delete this?")){
            fireDb.child(`EmployeeDetials/${id}`).remove((err)=>{
                if(err){
                    toast.error(err);

                }else{
                    toast.success("Details Deleted succesfully!");
                }
            });

        }
    };

    // fetch data from db to table 
    return (
        <div style={{marginTop:"100px"}} className="tbl">
           <table className="styled-table">
           <thead>
           <tr>
           <th style={{textAlign:"center"}}>Sno</th>
           <th style={{textAlign:"center"}}>Name</th>
           <th style={{textAlign:"center"}}>EmpID</th>
           <th style={{textAlign:"center"}}>EmailID</th>
           <th style={{textAlign:"center"}}>EntityID</th>
           <th style={{textAlign:"center"}}>CorporateName</th>
           <th style={{textAlign:"center"}}>Mobileno</th>
           {/* <th style={{textAlign:"center"}}>Action</th> */}
           </tr>
           </thead>
           <tbody>
               {Object.keys(data).map((id, index) => {
                   return(
                       <tr key={id}>
                           <th scope="row">{index+1}</th>
                           <td>{data[id].name}</td>
                           <td>{data[id].empid}</td>
                           <td>{data[id].primaryemailid}</td>
                           <td>{data[id].entityid}</td>
                           <td>{data[id].corporatename}</td>
                           <td>{data[id].mobileno}</td>
                           {/* <td>
                           <Link to={`/view/${id}`}>
                                   <button className="btn btn-view">View</button>
                               </Link>
                               <Link to={`/update/${id}`}>
                                   <button className="btn btn-edit">Edit</button>
                               </Link>
                               <button className="btn btn-delete"
                               onClick={()=>onDelete(id)}>Delete</button>
                           </td> */}
                       </tr>
                   );
               }
              )}
           </tbody>

           </table>
        </div>
    );
};

export default Home;
