import React,{useState,useEffect}from 'react';
import {useHistory,useParams } from "react-router-dom";
import "./AddEmployee.css";
import fireDb from '../firebase';
import {toast} from "react-toastify";

// documenting the object
const initialState ={
    name:"",
    empid:"",
    primaryemailid:"",
    entityid:"",
    corporatename:"",
    mobileno:"",
};

// mainfunction for the AddEmployee
const AddEmployee = () => {


    const [state, setState] =useState(initialState);

    const [data,setData] =useState({});
    const{name,empid,primaryemailid,entityid,corporatename,mobileno} =state;


    const history = useHistory();

  //fetch data to update on the addemployee page.
    const {id} = useParams();
      useEffect(()=>{
        fireDb.child(`EmployeeDetials`).on("value",(snapshot) =>{
        if(snapshot.val() !== null){
          setData({...snapshot.val() });
        }else{
          setData({});
        }
      });
      return() =>{
        setData({});
      };
      
    },[id]);


    //runs only id with data on the data base
    useEffect(()=>{
      if(id){
        setState({...data[id]});
      }else{
        setState({...initialState});
      }
      return() =>{
        setState({...initialState});
      };

    },[id, data]);

//pass the input in input field
    const handleInputChange = (e)=>{
        const{name, value} =e.target;
        setState({...state,[name]:value});
    };

    //function for toast alerts if data filled or not filled.
    const handleSubmit = (e)=>{
        e.preventDefault();
        if(!name || !empid || !primaryemailid || !entityid || !corporatename||! mobileno) {
            toast.error("Please fill Required Fields!");
        } else {
          if(!id){
            fireDb.child("EmployeeDetials").push(state, (err) =>{
              if (err) {
                toast.error(err);
              }else{
                toast.success("Employee Added Successfully!");
              }
             
            });
            ///func for updating the record with id
          }else{
            fireDb.child(`EmployeeDetials/${id}`).set(state, (err) =>{
              if (err) {
                toast.error(err);
              }else{
                toast.success("Employee Updated Successfully!");
              }
             
            });
          }
          setTimeout(()=> history.push("/"),500);
        }
    };

    //form to insert data.
    return (

        <div style={{ marginTop: "100px" }}>
        <div style={{marginTop:"150px"}}>
        <div className="card">
            <div className="card-header">
            <div className="h1"><p>{id? "Update Employee Details":"Add Employee Details"}</p></div>
            </div>
            <div className="container">
           
            <br />
             <form
          style={{
            margin: "auto",
            padding: "5px",
            maxWidth: "300px",
            alignContent: "center",
            // textAlign:"left",
            fontWeight:"bolder",
            fontFamily:"sans-serif"
          }}
          onSubmit={handleSubmit}
        >
        <div><label htmlFor="name">Name</label></div>
        <input 
            type="text"
            value={name || ""}
            id="name"
            name="name"
            placeholder="John Doe"
            onChange={handleInputChange}
        />
          <br />
          <br />
         <label htmlFor="empid">Employee ID</label>
        <input 
            type="text"
            value={empid || ""}
            id="empid"
            name="empid"
            placeholder="MA1234"
            onChange={handleInputChange}
        />
        <br />
          <br />
          <label htmlFor="primaryemailid">Primary EmailID</label>
        <input 
            type="email"
            value={primaryemailid || ""}
            id="primaryemailid"
            name="primaryemailid"
            placeholder="johndoe@xyz.com"
            onChange={handleInputChange}
        />
         <br />
          <br />
          <label htmlFor="entityid">Entity ID</label>
        <input 
            type="number"
            value={entityid || ""}
            id="entityid"
            name="entityid"
            placeholder="1"
            onChange={handleInputChange}
        />
         <br />
          <br />
          <label htmlFor="corporatename">Corporate Name</label>
        <input 
            type="text"
            value={corporatename || ""}
            id="corporatename"   
            name="corporatename"
            placeholder="Mediassist TPA Pvt Ltd"
            onChange={handleInputChange}
        />
         <br />
          <br />
          <label htmlFor="mobileno">Mobile no</label>
        <input 
            type="number"
            value={mobileno || ""}
            id="mobileno"
            name="mobileno"
            placeholder="123456789"
            onChange={handleInputChange}
        />
         <br />
          <br />
          <div className="bau"><input type="submit" value= {id? "Update":"Add"}/></div>
          </form>
      
            </div>
            
        </div>
        </div>
          


        </div>
//...;;
    );
};

export default AddEmployee;
